import React, { Component } from 'react'
import Web3 from 'web3'
import './css.css';



class ShopMeta extends Component {
constructor(props){
    super()
  

}

async reload(){

}


async save(){
    let addr="0xfa28ED428D54424D42ED4F71415315df2f2E49D6"
    window.ethereum.enable().then(async function (accounts) { 
        const meta = new web3.eth.Contract(METAABI, META) 
        await meta.methods.setMeta(addr,document.getElementById("Shop_meta_setval").value).send({from: accounts[0] , gas: 123456 })
        this.reload()
})  
}

    render(){

        return ( 
            <>
        <div className="Shop_meta_row">        
            <div className="Shop_meta_panel">
  
               <span className="Shop_menu_item right">shop metadata </span>
               <div className="clear"></div>
               <br></br>
               <div>
               <span>shop name:</span><br></br>
               <input id="Shop_meta_setname" className="Shop_meta_setval" type="text" ></input>
               </div>
               <div>
               <span> description:</span><br></br>
               <input id="Shop_meta_setdesc" className="Shop_meta_setval" type="text" ></input>
               </div>
               <div>
               <span> image url (512*512):</span><br></br>
               <input id="Shop_meta_setimage" className="Shop_meta_setval" type="text" ></input>
               </div>
               <div>
               <span> discord server:</span><br></br>
               <input id="Shop_meta_setdiscord" className="Shop_meta_setval" type="text" ></input>
               <br></br>
               </div>
               <div>
               <span> twitter:</span><br></br>
               <input id="Shop_meta_settwitter" className="Shop_meta_setval" type="text" ></input>
               </div>
               <input className="Shop_meta_setprice_button" type="button" value=" save" onClick={() => this.save()}></input>
               <br></br><br></br>     
            </div>
        </div>
       </>
            
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

let METAABI=[ { "inputs": [ { "internalType": "address", "name": "_contract", "type": "address" }, { "internalType": "string", "name": "val", "type": "string" } ], "name": "setMeta", "outputs": [ { "internalType": "bool", "name": "", "type": "bool" } ], "stateMutability": "nonpayable", "type": "function" }, { "inputs": [ { "internalType": "address", "name": "", "type": "address" } ], "name": "meta", "outputs": [ { "internalType": "string", "name": "", "type": "string" } ], "stateMutability": "view", "type": "function" } ]
let META="0x78Ce3F815cf78a250ACb5790b6e039114dE3A1b3"


        
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")




export default ShopMeta