import React, { Component } from 'react'
import './css.css';


class Strategy extends Component {

    render(){
        console.log(this.props.update) 
        return ( 
            <div id="Strategy_panel">
                <span className="Strategy_logo" aria-labelledby="jsx-a11y/accessible-emoji" role="img"></span>
            <div id="Strategy_mainlabel">Strategy</div>
            <br></br><br></br>
            <div className="Strategy_minipanel">

               
                <div className="clear"></div>
                <center className="Strategy_name">cycle of beans #1</center>
                <img className="Strategy_sketch"  alt="cycle of beans"  src="https://i.imgur.com/QtBSZrP.png"/>
                <div className="Strategy_desc"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">The Main HappyFarm DFO Participation Flow Strategy</span></div>
                <input  className="Strategy_more_button" type="button" value=" CYCLE OF BEANS " onClick={() => this.props.update("UnderConstruction")}/>
            </div>
            
            <div className="Strategy_minipanel">
                <div className="Strategy_image"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img"></span></div>
                <div className="Strategy_label"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">strategy 2</span></div>
                <div className="clear"></div>
            </div>

            <div className="Strategy_minipanel">
                <div className="Strategy_image"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img"></span></div>
                <div className="Strategy_label"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">strategy 3</span></div>
                <div className="clear"></div>
            </div>
            <div className="clear"/>
            <input  id="Strategy_back_button" type="button" value=" BACK" onClick={() => this.props.update("HoneyPot")}/>
       
           <div id="Strategy_underconstr">Under Construction!</div>   
                 </div>
            
        );
    }


}




export default Strategy