import React, { Component } from 'react'
import Web3 from 'web3'
import ProposalSoftInflator from '../Proposals/ProposalSoftInflator';
import UnderConstruction from '../UnderConstruction/UnderConstruction';
import './css.css';


class Micros extends Component {
   

    
    componentDidMount() {
        this.loadBlockchainData()
    }
    
    
        async loadBlockchainData() {
            window.ethereum.enable().then(async function (accounts) {  
                let dfo=""; 
                if(getUrlVars()['dfo'])dfo=getUrlVars()['dfo']
                const DFOHub = require('dfo-hub');
                DFOHub(web3);
                //DFOHub is now fully loaded in your Blockchain Provider object
                var myDFO = await web3.eth.dfoHub.load(dfo)
                document.getElementById("Micros_header_dfo").innerHTML=' '+myDFO.name
                document.getElementById("Micros_browser_back_to_dfo").value=" "+myDFO.name;

            })
        }


    render(){
        console.log(this.props.update)
        let micro=this.props.stateX.micro

        return ( 
        <div id="Micros_flyer">
            <div id="Micros_mainlabel"><img id="Micros_logo" src="https://i.imgur.com/cMW68I6.png"></img> Microservices</div>
            <div id="Micros_browser">
                <input  id="Micros_browser_back_to_dfo" className="Micros_browser_button" type="button" value=" DFO" onClick={() => this.props.update("DFO")}/> 
                <input className="Micros_browser_button" type="button" value=" Installed" onClick={() => this.props.update("DFOManager")}/>      
            </div>
            <div id="Micros_left_column">
            <input  className="Micros_button" type="button" onClick={() => this.props.micro("ProposalSoftInflator")} value=" Slow Inflator" />
            <input  className="Micros_button" type="button" onClick={() => this.props.micro("ciao")} value=" Standard Gamble" />
            <input  className="Micros_button" type="button" onClick={() => this.props.micro("ciao")} value=" HappyFlux" />
            <input  className="Micros_button" type="button" onClick={() => this.props.micro("ciao")} value=" Airdropper" />
            <input  className="Micros_button" type="button" onClick={() => this.props.micro("ciao")} value=" HappyLottery" />
            <input  className="Micros_button" type="button" onClick={() => this.props.micro("ciao")} value=" Under Construction" />
            <br></br><br></br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Under Construction
 
            </div>
            <div id="Micros_right_column">
                <div id="Micros_header">Proposal to: <div id="Micros_header_dfo">HappyFarm</div></div>
       {(micro==="ProposalSoftInflator")?<ProposalSoftInflator/>:<UnderConstruction/>}

            </div>
            <div id="Micros_footer">
                <input  className="Micros_footer_button" type="button" value="  Res & Dev" onClick={() => this.props.update("Research")}/>       
            </div>
        </div>
        );
    }


}

