let BEANS="0x801F90f81786dC72B4b9d51Ab613fbe99e5E4cCD"

const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}


export default Micros