import React, { Component } from 'react'
import Web3 from 'web3';
import ShopToken from '../ShopToken/ShopToken'
import ShopModulesPanel from '../ShopModulesPanel/ShopModulesPanel'
import BoxModules from '../BoxModules/BoxModules'
import SingleShopMenu from '../SingleShopMenu/SingleShopMenu'
import ShopMenu from '../ShopMenu/ShopMenu'
import ShopMeta from '../ShopMeta/ShopMeta'
import PRiceList1_0 from '../PRiceList1_0/PRiceList1_0'

import './css.css';


class Prices extends Component {
 constructor(){
     super()
     this.state={
         edit:false,
         items:[],
         box:"",
         boxindex:0,
         ticket:"",
         ticketindex:0,
         mode:"default",
         amountItems:0,
         wallet:""
        }
     this.showBox = this.showBox.bind(this);
     this.showTicket = this.showTicket.bind(this);
     this.setMode=this.setMode.bind(this)
     this.setAmountItems=this.setAmountItems.bind(this)
 }

    async showBox(addr,ind){
        this.setState({box:addr,boxindex:ind})
        this.render()
    }

    componentDidMount(){
       this.setWallet()
    }

    async setWallet(){
        if(!getUrlVars()["shop"]){
            let account
            await window.ethereum.enable().then(function (accounts) {
            account=accounts[0]
            document.getElementById("Shop_social").innerHTML=getAvatar(account)
            })
            this.setState({wallet:account})
            this.render()
           
        }
        if(getUrlVars()["shop"]>""){
            let t=getUrlVars()["shop"]
            await window.ethereum.enable().then(async function (accounts) {
            const tick = new web3.eth.Contract(ticketABI, t) 
            let box = await tick.methods.box().call()
            const boxx = new web3.eth.Contract(boxABI, box) 
            t = await boxx.methods.master().call()
            try{document.getElementById("Shop_social").innerHTML=getAvatar(t)}catch(e){}  
                  })
            this.setState({wallet:t})
            this.render()
           //document.getElementById("Shop_social").innerHTML=getAvatar(t)
           
        }
    }

    async showTicket(addr,ind){
        this.setState({ticket:addr,ticketindex:ind})
        this.render()
    }

    async setAmountItems(i){
        try{document.getElementById("Shop_amount").innerHTML="Items: "+i+" "}catch(e){}
    this.setState({amountItems:i})
    }


         editMode(){
            (!this.state.edit)?this.setState({edit:true}):this.setState({edit:false})
        }


        setMode(md){
this.setState({mode:md})
        }

     
    render(){


const final = [];
let n=0;
if(getUrlVars()["shop"]>""){
return(
        <div id="Shop_panel">
                <div id="Shop_mainlabel"><span id="stakecontract" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => jump()}> </span> HappyShop</div>
                <div id="Shop_items_container">
                    <div className="Shop_row">
                        <div className="Shop_label">
                            <span id="Shop_settings" className="" onClick={() => this.editMode()}></span>
                            {(!this.state.edit)?
                            <div id="Shop_amount" className="right">Items: {this.state.amountItems} </div>
                            :
                            <span id="Shop_social" className="right">{getAvatar(this.state.wallet)}</span>
                            }
                        </div>            
                    </div>
                    <div className="clear"/>    
                </div>
                {(this.state.edit)?<SingleShopMenu setMode={this.setMode}></SingleShopMenu>:<></>}
                {
                            (this.state.mode=="default")?
                            <>
                            <PRiceList1_0 edit={this.state.edit} address={getUrlVars()["shop"]} index={0} setAmount={this.setAmountItems}></PRiceList1_0>
                            <ShopMeta></ShopMeta>
                            </>
                        :
                            <>
                            <center><img id="menu_instructions_image" src="https://i.imgur.com/6ygtcQI.png"></img></center>
                            </>
                        }
                
            </div>
     
);
}else{

  
        return ( 
        <div id="Shop_panel">
            <div id="Shop_mainlabel"><span id="stakecontract" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => jump()}> </span> HappyShop</div>
                <div id="Shop_items_container">
                    <div className="Shop_row">
                        <div className="Shop_label"><span id="Shop_settings" className="" onClick={() => this.editMode()}></span>
                        
        <div id="Shop_social" className="right">{getAvatar(this.state.wallet)}</div>
                        
                        </div>            
                    </div>
                    <div className="clear"/>
                  

                    
                </div>
                
                {(!this.state.edit)?
                    <>
                    <ShopMenu setMode={this.setMode}></ShopMenu>
                    {(this.state.mode=="default")?
                        <>
                        <BoxModules showBox={this.showBox}></BoxModules>           
                        <ShopModulesPanel db={this.state.box} boxindex={this.state.boxindex} ticket={this.showTicket}></ShopModulesPanel>
                        </>
                    :
                        <>{
                            (this.state.mode=="meta")?
                            <ShopMeta></ShopMeta>
                        :
                            <>
                            <center><img id="menu_instructions_image" src="https://i.imgur.com/6ygtcQI.png"></img></center>
                            </>
                        }</>
                    }
                    </>
                :
                    <div></div>

                }
                    {(this.state.ticket!="")?
                    <PRiceList1_0 edit={((this.state.ticket!="")&&(this.state.edit))} address={this.state.ticket} index={this.state.ticketindex}></PRiceList1_0>
        :<></>
                                }
                                            </div>
        );

        }
    }


}

    function getAvatar(resp){
        let res
        if(resp)res=resp.toLowerCase()
        if(!resp)res="xxx"

        let avatar="";
        if(res.slice(-1)==="0")avatar="";
        if(res.slice(-1)==="1")avatar="";
        if(res.slice(-1)==="2")avatar="";
        if(res.slice(-1)==="3")avatar="";
        if(res.slice(-1)==="4")avatar="";
        if(res.slice(-1)==="5")avatar="";
        if(res.slice(-1)==="6")avatar="";
        if(res.slice(-1)==="7")avatar="";
        if(res.slice(-1)==="8")avatar="";
        if(res.slice(-1)==="9")avatar="";
        if(res.slice(-1)==="a")avatar="";
        if(res.slice(-1)==="b")avatar="";
        if(res.slice(-1)==="c")avatar="";
        if(res.slice(-1)==="d")avatar="";
        if(res.slice(-1)==="e")avatar="";
        if(res.slice(-1)==="f")avatar="";
        return avatar;
    }

function jump(){
    window.open('https://etherscan.io/address/0x7d410AFA45377006A0F79Ae6157A6A873Bfa5567')
}


function fire(){}




function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

