import React, { Component } from 'react'
import Web3 from 'web3'
import './css.css';



class ShopToken extends Component {
constructor(props){
    super()
  

}

async buy(){
        let addr=this.props.address
        let index=this.props.index
        window.ethereum.enable().then(async function (accounts) { 
            const door = new web3.eth.Contract(TICKETABI, TICKET) 
            door.methods.buy(addr,accounts[0]).send({from: accounts[0] , gas: 123456 , value: document.getElementById("cost_"+index).value})
    })  
}

async setPrice(){
    let addr=this.props.address
    let index=this.props.index
    let priceList=this.props.priceList
    window.ethereum.enable().then(async function (accounts) { 
        const prices = new web3.eth.Contract(PriceListABI, priceList) 
        prices.methods.setPrice(addr,parseInt(document.getElementById("Shop_item_setprice_field_"+index).value)*1000).send({from: accounts[0] , gas: 123456 })
    })  
}

calculate_price(){
    let price=parseInt(document.getElementById('Shop_item_setamount_field_'+this.props.index).value)*parseInt(this.props.price)/1000
    document.getElementById("cost_"+this.props.index).style="text-align:right;"
    document.getElementById('cost_'+this.props.index).value=price
}

async loadToken(){
    let addr=this.props.address
    let index=this.props.index
    
    await window.ethereum.enable().then(async function (accounts) { 
        try{  
        const tkn = new web3.eth.Contract(ABI, addr) 
        let name = await tkn.methods.name().call()
        document.getElementById("Shop_item_"+index).innerHTML=name
        let dec = await tkn.methods.decimals().call()
        let symbol = await tkn.methods.symbol().call()
        let bal = await tkn.methods.balanceOf(GIFTER).call()/Math.pow(10,dec)
        document.getElementById("Shop_item_setamount_field_"+index).value=bal
        document.getElementById("cost_"+index).style="text-align:left;"
        document.getElementById("cost_"+index).value=symbol
        }catch(e){}
})
this.render()
}

    componentDidMount(){
        this.loadToken()
      
    }

    render(props){
    console.log(props)

    
        return ( 
        <div className="Shop_row">        
            <div className="Shop_item_panel">
               <img className="Shop_item_icon" onClick={() => window.open('https://etherscan.io/address/'+this.props.memory)} src="https://dapp.dfohub.com/assets/img/default-logo.png"></img>
               <div id={"Shop_item_"+this.props.index} className="Shop_item_label">loading...</div>
               <img className="Shop_item_price_icon" src="https://i.imgur.com/FPL7c5a.png"></img>
               <span className="Shop_item_price right">{parseInt(this.props.price)/1000}</span>
               <div className="clear"></div>
                {(this.props.edit)?
                <div className="Shop_item_setprice">
                    <input id={"Shop_item_setprice_field_"+this.props.index} className="Shop_item_setprice_field" type="text"></input>
                    <input className="Shop_item_setprice_button" type="button" value=" Set Price" onClick={() => this.setPrice()}></input>
                </div>
                :
               <div className="Shop_item_setprice">
                   <input id={"Shop_item_setamount_field_"+this.props.index} className="Shop_item_setamount_field" type="text" onKeyUp={() => this.calculate_price()} onKeyPress={() => this.calculate_price()}></input>
                   <img className="Shop_item_icon"  src="https://dapp.dfohub.com/assets/img/default-logo.png"></img>
                   <input  id={"cost_"+this.props.index} className="Shop_item_setamount_field" type="text"  onKeyUp={() => this.calculate_price()} onKeyPress={() => this.calculate_price()}></input>
                   <img className="Shop_item_icon"  src="https://i.imgur.com/FPL7c5a.png"></img>
                   <input className="Shop_item_setprice_button" type="button" value=" buy!" onClick={() => this.buy()}></input>
               </div>
    }
                
               </div>
        </div>
            
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

