import React, { Component } from 'react'
import Web3 from 'web3'
import Ticket1_0 from '../Ticket1_0/Ticket1_0';
import TicketInstaller1_0 from '../Ticket1_0/TicketInstaller1_0';

import './css.css';



class InstallerManager extends Component {
constructor(props){
    super()
    this.loadModules = this.loadModules.bind(this)
    this.state={
        focus:"",
    enabled_items:[],
    disabled_items:[],
    ticket1_0installer:false
}
this.renderAgain = this.renderAgain.bind(this);
this.enable = this.enable.bind(this);
}

async getCode(address){
    console.log("getCode"+address)
    let t=0
try{
let module = new web3.eth.Contract(TICKETABI, address)
let codex=await module.methods.code().call()
t=codex
}catch(f){}
return t
}

renderAgain(){
 
 this.props.reset()
    this.render()
}

enable(factory){
 if(factory=="ticket1_0installer")
    this.setState({ticket1_0installer:true})
    this.render()
}

    componentDidMount(){
      //this.loadModules()
      this.loadInstallers()
      this.render()
    }

    async loadInstallers(){
        let db=this.props.db
        let a=[]
        await window.ethereum.enable().then(async function (accounts) {   
            const happybox = new web3.eth.Contract(HAPPYBOXABI, db) 
let t=await happybox.methods.modules(TICKETFACTORY).call()
            a.push(t)
          console.log(t)
        })
        this.setState({focus:db})
        await this.setState({ticket1_0installer:a[0]})
        this.render()
    }

  
  

   async loadModules(){
    let total_modules=[]
    let total_disabled_modules=[]
    let db=this.props.db
    let rendAgain=this.renderAgain
    await window.ethereum.enable().then(async function (accounts) {   
        const happybox = new web3.eth.Contract(HAPPYBOXABI, db) 
        
        let count=0;
        for(let i=0;i<count+1;i++){
            let addr
            let found=false
            try{
                addr=await happybox.methods.modules_list(i).call()
            console.log("MODULE ADDRESS: "+addr)
                found=true
                count++
            }catch(e){}  
            if(found){
                
                
                let code=0
                
                
                try{
                let module = new web3.eth.Contract(TICKETABI, addr)
                let codex=await module.methods.code().call()
                code=codex
                }catch(f){}
                

                let enabled=await happybox.methods.modules(addr).call()
                if(enabled){
                    console.log("yes")
                    let t
                if(code==0)t=<Ticket1_0 address={addr} key={i} enabled={true} box={db} reset={rendAgain}></Ticket1_0>
                if(code==1)t=<Ticket1_0 address={addr} key={i} enabled={true} box={db} reset={rendAgain}></Ticket1_0>
                if(code==2)t=<Ticket1_0 address={addr} key={i} enabled={true} box={db} reset={rendAgain}></Ticket1_0> 
                    total_modules.push(t)
                }else{
                    console.log("no")
                    let t
                    if(code==0)t=<Ticket1_0 address={addr} key={i} enabled={false} box={db} reset={rendAgain}></Ticket1_0>
                    if(code==1)t=<Ticket1_0 address={addr} key={i} enabled={false} box={db} reset={rendAgain}></Ticket1_0>
                    if(code==2)t=<Ticket1_0 address={addr} key={i} enabled={false} box={db} reset={rendAgain}></Ticket1_0>
                    total_disabled_modules.push(t)
                }
              
            }
            
        console.log(`modules: ${count}`)
        }
    
    })
    this.setState({enabled_items : total_modules})
    this.setState({disabled_items : total_disabled_modules})
    this.render()
}

    render(props){
    console.log(this.state.enabled_items)
    if(this.props.db!=this.state.focus)this.loadInstallers()
    let addr
          let enabled
            let i
        return ( 
            
        <div className="Installer_Modules_Panel">
            <img id="Shop_add_item_icon" src="https://i.imgur.com/YKhumij.png"></img>
            <div className="clear"></div> 
            <div className="Installer_Modules_Container">  
            
            <TicketInstaller1_0 address={addr} key={i} enabled={this.state.ticket1_0installer} box={this.props.db} reset={this.renderAgain} enable={this.enable}></TicketInstaller1_0>
              
            </div>
        </div>
            
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

