import React, { Component } from 'react'
import Web3 from 'web3';
import ShopToken from '../ShopToken/ShopToken'
import ShopModulesPanel from '../ShopModulesPanel/ShopModulesPanel'
import BoxModules from '../BoxModules/BoxModules'

import './css.css';


class PRiceList1_0 extends Component {
 constructor(props){
     super()
     this.state={
         edit:false,
         items:[],
         box:"",
         boxindex:0,
         ticket:"",
         focus:""
        }
 }

    componentDidMount() {
        this.loadItems()
        
    }




    async setPrice(){
        let addr=this.props.address
        await window.ethereum.enable().then(await async function (accounts) { 
            const priceListC = new web3.eth.Contract(ticketABI, addr) 
                let priceListz = await priceListC.methods.prices().call()
                console.log("priceListz",priceListz)
                const prices = new web3.eth.Contract(PriceListABI, priceListz) 
            if(document.getElementById("Shop_add_item_setprice_field").value!=""){
            if(parseFloat(document.getElementById("Shop_add_item_setprice_field").value)>0){
            await prices.methods.setPrice(document.getElementById("Shop_add_item_setaddress_field").value,parseInt(parseFloat(document.getElementById("Shop_add_item_setprice_field").value)*1000)).send({from: accounts[0] , gas: 323456 })
            }else{
                alert("SET A PRICE > 0")
            }    
        }else{
                alert("SET A TOKEN ADDRESS IN ORDER TO ADD A TOKEN TO THE SHOP!")
            }
         
        })
        this.loadItems()
        
    }



    async loadItems() {
      
        let total_listing=[]

        let addr=this.props.address
        let priceListz
        let box
        await window.ethereum.enable().then(async function (accounts) {
            let account=accounts[0];
            try{
            const tick = new web3.eth.Contract(ticketABI, addr) 
            priceListz = await tick.methods.prices().call()
            box = await tick.methods.box().call()
            const prices = new web3.eth.Contract(PriceListABI, priceListz) 
            let index = await prices.methods.priceListing(0).call()
            for(let i=0;i<parseInt(index[2]);i++){
                let item = await prices.methods.priceListing(i).call()
                console.log("tkn",item[0])
                try{
                const tkn = new web3.eth.Contract(ABI, item[0]) 
                
                let name = await tkn.methods.totalSupply().call()
                console.log("tkn",name)
                let a=[]
                a.push(item)
                a.push(name)
                total_listing.push(a)
                }catch(e){}
               
            }
            console.log("LOADED ITEMS "+total_listing)
            
        }catch(e){}
        })

        this.setState({items : total_listing,priceList : priceListz,focus : addr,box : addr})
        this.props.setAmount(total_listing.length)
        //this.render()
        
            
        }


         editMode(){
           // alert("STATE: "+this.state.edit)
            (!this.state.edit)?this.setState({edit:true}):this.setState({edit:false})
        }

    render(){
const final = [];
let n=0;

if(this.props.address!=this.state.focus)this.loadItems()
  for (let  itm of this.state.items) {  
    final.push(<ShopToken priceList={this.state.priceList} address={itm[0][0]} price={itm[0][1]} index={n++} key={n++} edit={this.state.edit} name={itm[1]}></ShopToken>);
  }
        return ( 
        
            
                
                <div>
                  
                {(final.length>0)?
                <div>
                {(this.props.edit)?
                <div className="Shop_module_item_row">
                    <div className="Shop_module_link_panel">
                    {(!getUrlVars()["shop"])?<span className="Price_list_label">#{this.props.index} ticket panel</span>:<span className="Price_list_label"><a  className="ticket_link" href={"https://etherscan.io/address/"+this.state.focus} target="_blank"></a></span>}
                    {(!getUrlVars()["shop"])?<span className="Shop_module_link_icon right" onClick={() => jump(this.state.focus)}></span>:<></>}
                            <span className="Shop_module_link_icon right" onClick={() => jumpBox(this.state.box)}></span>
                     </div>       
                </div>:
                <></>}
                {final}
                </div>
                :
                <>
                {(this.props.edit)?<div className="empty_message">#{this.props.index} sell something!</div>:<></>}
                {(this.props.edit)?
                <div className="Shop_add_item_row">
                    <div className="Shop_module_link_panel">
                    {(!getUrlVars()["shop"])?<span className="Price_list_label">#{this.props.index} ticket panel</span>:<span className="Price_list_label"><a className="ticket_link" href={"https://etherscan.io/address/"+this.state.focus} target="_blank"></a></span>}
                    {(!getUrlVars()["shop"])?<span className="Shop_module_link_icon right" onClick={() => jump(this.state.focus)}></span>:<></>}
                            <span className="Shop_module_link_icon right" onClick={() => jumpBox(this.state.box)}></span>
                     </div>       
                </div>:
                <></>}
                </>
                }
                {(this.props.edit)?
                <div>
                <div className="Shop_add_item_row">
                    <div className="Shop_add_item_panel">
                            <img id="Shop_add_item_icon" src="https://i.imgur.com/YKhumij.png"></img>
                            <div className="Shop_item_setprice">
                                <input id="Shop_add_item_setaddress_field" className="Shop_item_setprice_field" type="text"></input>
                                <span className="right paddingright">address</span>
                            </div>
                            <div className="Shop_item_setprice">
                                <input id="Shop_add_item_setprice_field" className="Shop_add_item_setprice_field" type="text"></input>
                                <input className="Shop_item_setprice_button" type="button" value=" Set Price" onClick={() => this.setPrice()}></input>
                            </div>
                    </div>
                </div>
                </div>:
                    <div></div>
                    }
        </div>
        );
    }


}

function jumpBox(addr){
    window.open('https://etherscan.io/address/'+addr)
}

function jump(addr){
    window.open('?shop='+addr)
}


function fire(){}




function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

