import React, { Component } from 'react'
import Web3 from 'web3'
import './css.css';



class TicketInstaller1_0 extends Component {
constructor(props){
    super()


}

async installNow(){
    
    let addr=this.props.address
    let db=this.props.box
    await window.ethereum.enable().then(async function (accounts) {   
        const factory = new web3.eth.Contract(TICKET1_0FACTORYABI, TICKET1_0FACTORY) 
        await factory.methods.install(db).send({gas: 1000000, from: accounts[0]})
        //reload 
    })
    this.props.reset()
}

async enableNow(){
    
    let addr=this.props.address
    let db=this.props.box
    await window.ethereum.enable().then(async function (accounts) {   
        const happybox = new web3.eth.Contract(HAPPYBOXABI, db) 
        await happybox.methods.set(TICKET1_0FACTORY,true,1).send({gas: 123456, from: accounts[0]})
        //reload 
    })
    this.props.enable("ticket1_0installer")
}


    render(props){
    console.log(props)
   
            return ( <div>
                        
                <div className={(this.props.enabled)? "Ticket_Modules_Installer" : "Ticket_Modules_Installer_disabled" }> 
                  
                
    <span className="Ticket_module_icon"> </span> ticket 1.0 installer
                
                                <br></br> 
                                accepts payment in ETH
                                <br></br>
                                {(this.props.enabled)?
                                <input className="Ticket_module_install_button" onClick={() => this.installNow()} type="button" value=" install"></input>              
                                :
                                <input className="Ticket_module_install_button" onClick={() => this.enableNow()} type="button" value=" enable"></input>              
                                 }
                                </div>
                        </div>         
                );   
        
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

