import React, { Component } from 'react'
import Splash from '../Splash/Splash';
import Beanometer from '../Beanometer/Beanometer';
import HappyFarm from '../HappyFarm/HappyFarm';
import Beans from '../Beans/Beans';
import DFO from '../DFO/DFO';
import Instructions from '../Instructions/Instructions';
import Avatar from '../Avatar/Avatar';
import HappyLand from '../HappyLand/HappyLand';
import Math from '../Math/Math';
import Token from '../Token/Token';
import ProxyVision from '../ProxyVision/ProxyVision';
import ProxyRoad from '../ProxyRoad/ProxyRoad';
import HappyProxy from '../HappyProxy/HappyProxy';
import Research from '../Research/Research';
import Micros from '../Micros/Micros';
import HoneyPot from '../HoneyPot/HoneyPot';
import Strategy from '../Strategy/Strategy';
import HoneyRoad from '../HoneyRoad/HoneyRoad';
import Distribution from '../Distribution/Distribution';
import HappyGraph from '../HappyGraph/HappyGraph';
import GraphVision from '../GraphVision/GraphVision';
import Friends from '../Friends/Friends';
import Lottery from '../Lottery/Lottery';
import Empty from '../Empty/Empty';
import EmptyAvatar from '../EmptyAvatar/EmptyAvatar';
import DFOManager from '../DFOManager/DFOManager';
import UnderConstruction from '../UnderConstruction/UnderConstruction';
import Stake from '../Stake/Stake';
import Prices from '../Prices/Prices';
import './css.css'; 


class Pad extends Component {
    constructor() {
        super()
        this.state = {
          layout: "_1_",
          items: []
        }
      }

    layoutBack = (l) => {
        this.setState({
            layout : JSON.parse(l)
        })     
    }  

    enumBack = (e) => {
        this.setState({ layout : e })
    }

    itemsBack = (e) => {
      this.setState({ items : e })
  }
   

    render(){ 
      if(this.props.stateX.panel==="UnderConstruction")
      return (
        <div id="superpad">
          <div id="pad">
            <UnderConstruction update={this.props.update}></UnderConstruction>
          </div>
        </div>
      );
      if(this.props.stateX.panel==="Splash")
        return (
          <div id="superpad">
            <div id="pad">
              <Splash update={this.props.update}></Splash>
            </div>
          </div>
        );
        if(this.props.stateX.panel==="Beanometer")
        return (
          <div id="superpad">
            <div id="pad">
              <Beanometer update={this.props.update}></Beanometer>
            </div>           
          </div>
        );
        if(this.props.stateX.panel==="HappyFarm")
        return (
          <div id="superpad">
            <div id="pad">
              <HappyFarm update={this.props.update}></HappyFarm>
            </div>           
          </div>
        );
        if(this.props.stateX.panel==="Beans")
        return (
          <div id="superpad">
            <div id="beans_pad">
              <Beans update={this.props.update}></Beans>
            </div>           
          </div>
        );
        if(this.props.stateX.panel==="DFO")
        return (
          <div id="superpad">
            <div id="DFO_pad">
              <DFO update={this.props.update}></DFO>
            </div>           
          </div>
        );
        if(this.props.stateX.panel==="Instructions")
        return (
          <div id="superpad">
            <div id="instructions_pad">
              <Instructions update={this.props.update}></Instructions>
            </div>           
          </div>
        );
        if(this.props.stateX.panel==="Math")
        return (
          <div id="superpad">
            <div id="Math_pad">
              <Math update={this.props.update}></Math>
            </div>           
          </div>
        );
        if(this.props.stateX.panel==="Avatar")
        return (
          <div id="superpad">
            <div id="Avatar_pad">
              
              <Avatar update={this.props.update}></Avatar>
            </div>           
          </div>
        );
        if(this.props.stateX.panel==="Token"){
        window.history.pushState('token_page', 'Token Display', '/?token='+this.props.stateX.token);
        return (
          <div id="superpad">
            <div id="pad">
              <Token update={this.props.update} token={this.props.stateX.token}></Token>
            </div>           
          </div>
        );}

        if(this.props.stateX.panel==="HappyLand"){
          window.history.pushState('token_page', 'Token Display', '/?token='+this.props.stateX.token);
          return (
            <div id="superpad">
              <div id="pad">
                <HappyLand update={this.props.update}></HappyLand>
              </div>           
            </div>
          );}
          if(this.props.stateX.panel==="Lottery"){
            return (
              <div id="superpad">
                <div id="pad">
                  <Lottery update={this.props.update}></Lottery>
                </div>           
              </div>
            );}
            if(this.props.stateX.panel==="HappyProxy"){
              return (
                <div id="superpad">
                  <div id="pad">
                    <HappyProxy update={this.props.update}></HappyProxy>
                  </div>           
                </div>
              );}
              if(this.props.stateX.panel==="Research"){
                return (
                  <div id="superpad">
                    <div id="pad">
                      <Research update={this.props.update}></Research>
                    </div>           
                  </div>
                );}
                if(this.props.stateX.panel==="ProxyRoad"){
                  return (
                    <div id="superpad">
                      <div id="pad">
                        <ProxyRoad update={this.props.update}></ProxyRoad>
                      </div>           
                    </div>
                  );}
                  if(this.props.stateX.panel==="ProxyVision"){
                    return (
                      <div id="superpad">
                        <div id="pad">
                          <ProxyVision update={this.props.update}></ProxyVision>
                        </div>           
                      </div>
                    );}
                    if(this.props.stateX.panel==="HoneyPot"){
                      return (
                        <div id="superpad">
                          <div id="pad">
                            <HoneyPot update={this.props.update}></HoneyPot>
                          </div>           
                        </div>
                      );}
                      if(this.props.stateX.panel==="Strategy"){
                        return (
                          <div id="superpad">
                            <div id="pad">
                              <Strategy update={this.props.update}></Strategy>
                            </div>           
                          </div>
                        );}
                        if(this.props.stateX.panel==="HoneyRoad"){
                          return (
                            <div id="superpad">
                              <div id="pad">
                                <HoneyRoad update={this.props.update}></HoneyRoad>
                              </div>           
                            </div>
                          );}
                          if(this.props.stateX.panel==="Micros"){
                            return (
                              <div id="superpad">
                                <div id="pad">
                                  <Micros update={this.props.update} micro={this.props.micro} stateX={this.props.stateX}></Micros>
                                </div>           
                              </div>
                            );}
                            if(this.props.stateX.panel==="Distribution"){
                              return (
                                <div id="superpad">
                                  <div id="pad">
                                    <Distribution update={this.props.update}></Distribution>
                                  </div>           
                                </div>
                              );}
                              if(this.props.stateX.panel==="HappyGraph"){
                                return (
                                  <div id="superpad">
                                    <div id="pad">
                                      <HappyGraph update={this.props.update}></HappyGraph>
                                    </div>           
                                  </div>
                                );}
                                if(this.props.stateX.panel==="GraphVision"){
                                  return (
                                    <div id="superpad">
                                      <div id="pad">
                                        <GraphVision update={this.props.update}></GraphVision>
                                      </div>           
                                    </div>
                                  );}
                                  if(this.props.stateX.panel==="Friends"){
                                    return (
                                      <div id="superpad">
                                        <div id="pad">
                                          <Friends update={this.props.update}></Friends>
                                        </div>           
                                      </div>
                                    );}
                                    if(this.props.stateX.panel==="Empty"){
                                      return (
                                        <div id="superpad">
                                          <div id="pad">
                                            <Empty update={this.props.update}></Empty>
                                          </div>           
                                        </div>
                                      );}
                                      if(this.props.stateX.panel==="EmptyAvatar"){
                                        return (
                                          <div id="superpad">
                                            <div id="pad">
                                              <EmptyAvatar update={this.props.update}></EmptyAvatar>
                                            </div>           
                                          </div>
                                        );}
                                        if(this.props.stateX.panel==="DFOManager"){
                                          return (
                                            <div id="superpad">
                                              <div id="pad">
                                                <DFOManager update={this.props.update}></DFOManager>
                                              </div>           
                                            </div>
                                          );}
                                          if(this.props.stateX.panel==="Prices"){
                                            return (
                                              <div id="superpad">
                                                <div id="pad">
                                                  <Prices update={this.props.update}></Prices>
                                                </div>           
                                              </div>
                                            );}
                                            if(this.props.stateX.panel==="Stake"){
                                              return (
                                                <div id="superpad">
                                                  <div id="pad">
                                                    <Stake update={this.props.update}></Stake>
                                                  </div>           
                                                </div>
                                              );}
                                         
                                          
                                          
    }

    jump = () => {
      return(
        window.open('[["pad"],'+JSON.stringify(this.state.layout).split('"_').join("{").split('_"').join("}")+']')
      );
    }

    renderLayoutString = () => {
      return ('[["pad"],'+JSON.stringify(this.state.layout).split('"_').join("{").split('_"').join("}"))+']';
    }

}



