import React, { Component } from 'react'
import './css.css';


class ProxyVision extends Component {
   
    render(){
        console.log(this.props.update) 
        return ( 
        <div id="ProxyVision_flyer">
             <br></br>
             
             <div id="ProxyVision_mainlabel">
             <center> ProxyCat Vision</center>
             </div>
             
             <div id="ProxyVision_label">
              GOAL:
             <br></br>
             <br></br>
             The creation of an autonomous aggregator of Ethereum data implies the relation with a big amount of data
             to be pulled from the blockchain.
             <br></br>
             <br></br>The idea of not having a web server to retrieve and organize all that data can be viable 
             only if the querying/aggregation logics is moved on-chain relieving the final client from 
             performing tons of calls to the blockchain itself (usually via metamask and not a local node!).
             <br></br>
             <br></br>
              ProxyCat
             <br></br>
             <br></br>
             ProxyCat borns to answer to this need.
             <br></br>
             Imagine it as a growing set of ready solidity functions intended to easily query Ethereum and get back data in json format.<br></br>
             ProxyCat can be conisdered the first try to transform Ethereum into a "json feed machine".
             <br></br>
             <br></br>
             Opensource and ready to be used by anyone, it is a collection of interfaces to the most common standard data such as tokens and DFOs and HappyFarm generic data.
             <br></br>
             <br></br>
             Ideally anyone could add calls to the library. In such scenario only the community as a whole can judge wether a function should be added or not, or wether a piece of code is actually doing what it is expected to.
             <br></br>
             <br></br>
             <img id="ProxyVision_image"  alt="BEANS"  src="https://i.imgur.com/gNeyjc7.png"/>
             <br></br>
             <br></br>

             Imagine so a feed library able to get any data from Ethereum and to be extended by anyone walking throught the collective filter.
             <br></br>
             <br></br><br></br>
              BEANS become the referring tool to rule  what is visible/not-visible on Ethereum.<br></br>
             They can be used to submit a new data source to popular attention via staking.<br></br>
             Or to increase visibility of specific data in the cloud of informations scanned by the  Cat. <br></br><br></br>
                         <input  className="ProxyVision_button" type="button" value=" BACK" onClick={() => this.props.update("HappyProxy")}/>
             </div>
        </div>
        );
    }


}




export default ProxyVision