import React, { Component } from 'react'
import Web3 from 'web3'
import './css.css';


class DFOManager extends Component {

   
    componentDidMount() {
        this.loadBlockchainData()
    }
    
    
        async loadBlockchainData() {
            window.ethereum.enable().then(async function (accounts) {  
                let dfo=""; 
                if(getUrlVars()['dfo'])dfo=getUrlVars()['dfo']
                const DFOHub = require('dfo-hub');
                DFOHub(web3);
                //DFOHub is now fully loaded in your Blockchain Provider object
                var myDFO = await web3.eth.dfoHub.load(dfo)
                document.getElementById("DFOManager_mainlabel").innerHTML=' '+myDFO.name
                document.getElementById("DFOManager_browser_back_to_dfo").value=" "+myDFO.name;

            })
        }


    render(){
        console.log(this.props.update) 
        return ( 
        <div id="DFOManager_panel">
            <div id="DFOManager_mainlabel"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img"></span>HappyFarm</div>
            <div id="DFOManager_mainlabel2">Installed Micros:</div>
            <div id="DFOManager_browser">
                <input  id="DFOManager_browser_back_to_dfo" className="DFOManager_browser_button" type="button" value=" DFO" onClick={() => this.props.update("DFO")}/>
                <input  id="DFOManager_browser_back_to_dfo" className="DFOManager_browser_button right" type="button" value=" NEW" onClick={() => this.props.update("Micros")}/>       
            </div>
                <div className="DFOManager">
                    <div className="DFOManager_image"><img className="DFOManager_micro_logo" src="https://i.imgur.com/cMW68I6.png"></img></div>
                    <div className="DFOManager_label">Microservice #1</div>
                    <div className="clear"></div>    
                </div>
                <div className="DFOManager">
                    <div className="DFOManager_image"><img className="DFOManager_micro_logo" src="https://i.imgur.com/cMW68I6.png"></img></div>
                    <div className="DFOManager_label">Microservice #2</div>
                    <div className="clear"></div>    
                </div>
                <div className="DFOManager">
                    <div className="DFOManager_image"><img className="DFOManager_micro_logo" src="https://i.imgur.com/cMW68I6.png"></img></div>
                    <div className="DFOManager_label">Microservice #3</div>
                    <div className="clear"></div>    
                </div>
                <div className="DFOManager">
                    <div className="DFOManager_image"><img className="DFOManager_micro_logo" src="https://i.imgur.com/cMW68I6.png"></img></div>
                    <div className="DFOManager_label">Microservice #4</div>
                    <div className="clear"></div>    
                </div>
                <div className="DFOManager">
                    <div className="DFOManager_image"><img className="DFOManager_micro_logo" src="https://i.imgur.com/cMW68I6.png"></img></div>
                    <div className="DFOManager_label">Microservice #5</div>
                    <div className="clear"></div>    
                </div>
                
        </div>
        );
    }


}

function jump(page){
    window.open(page)
  }

  
function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}


  const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")


export default DFOManager