import React, { Component } from 'react'
import Web3 from 'web3'
import Ticket1_0 from '../Ticket1_0/Ticket1_0';
import InstallerManager from '../InstallerManager/InstallerManager';
import './css.css';



class ShopModulesPanel extends Component {
constructor(props){
    super()
    this.loadModules = this.loadModules.bind(this)
this.state={focus:"",enabled_items:[],disabled_items:[]}
this.renderAgain = this.renderAgain.bind(this);
}

async getCode(address){
    console.log("getCode"+address)
    let t=0
try{
let module = new web3.eth.Contract(TICKETABI, address)
let codex=await module.methods.code().call()
t=codex
}catch(f){}
return t
}

renderAgain(){

    this.loadModules()
    this.render()
}

    componentDidMount(){
        if(this.props.db!="")
      this.loadModules()
    }


   async loadModules(){
    let total_modules=[]
    let total_disabled_modules=[]
    let db=this.props.db
    let rendAgain=this.renderAgain
    let ticket=this.props.ticket
    await window.ethereum.enable().then(async function (accounts) {   
        const happybox = new web3.eth.Contract(HAPPYBOXABI, db) 
        
        let count=0;
        for(let i=0;i<count+1;i++){
            let addr
            let found=false
            try{
            addr=await happybox.methods.modules_list(i).call()
            console.log("MODULE ADDRESS: "+addr)
                found=true
                count++
            }catch(e){}  
            if(found){
                
                
                let code=0
                
                
                try{
                let module = new web3.eth.Contract(TICKETABI, addr)
                let codex=await module.methods.code().call()
                code=codex
                }catch(f){}
                

                let enabled=await happybox.methods.modules(addr).call()
                if(enabled){
                    console.log("yes")
                    let t
                if(code==1)t=<Ticket1_0 address={addr} key={i} index={i} enabled={true} box={db} reset={rendAgain} ticket={ticket}></Ticket1_0>
                    total_modules.push(t)
                }else{
                    console.log("no")
                    let t
                    if(code==1)t=<Ticket1_0 address={addr} key={i} index={i} enabled={false} box={db} reset={rendAgain} ticket={ticket}></Ticket1_0>
                    total_disabled_modules.push(t)
                }
              
            }
            
        console.log(`modules: ${count}`)
        }
    
    })
    this.setState({focus:db})
    this.setState({enabled_items : total_modules})
    this.setState({disabled_items : total_disabled_modules})
    this.render()
}

    render(props){
    console.log(this.state.enabled_items)
    if(this.props.db!=this.state.focus)this.loadModules()
    
        return ( <>
            {(this.props.db!="")?
        <div className="Shop_Modules_Panel">
            <div className="Shop_Modules_index"> #{this.props.boxindex}</div>
            <div className="Shop_Modules_mainlabel"> modules  </div> 
            <div className="clear"></div> 
            <div className="Shop_Modules_Container">  
                {
                  this.state.enabled_items  
                } 
                {
                  this.state.disabled_items  
                }
            <InstallerManager db={this.props.db} reset={this.renderAgain}></InstallerManager>
              
            </div>
        </div>
        :<div></div>
            }
        </>
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

