import React, { Component } from 'react'
import './css.css';


class Lottery extends Component {
   
    render(){
        console.log(this.props.update) 
        return ( <div>
        <div id="Lottery_flyer">
             
             <div id="Lottery_mainlabel">A collaborative no-profit Lottery any Project/DFO can join to gain visibility and to promote staking of a token!
             <input  className="Lottery_button" type="button" value="PLAY LOTTERY " onClick={() => this.props.update("UnderConstruction")}/>
             <input  className="Lottery_button" type="button" value=" DFO INSTALL" onClick={() => this.props.update("Micros")}/>
             </div>
            <div id="Lottery_label">Research #3 - UnderConstruction</div>
            
        </div>
        <div id="Lottery_footer">
                <input  className="Lottery_footer_button" type="button" value="  Res & Dev" onClick={() => this.props.update("Research")}/>       
                <input  className="Math_footer_button" type="button" value="  Farming" onClick={() => this.props.update("HappyFarm")}/>  
                </div>
        </div>
        );
    }


}




export default Lottery