import React, { Component } from 'react'
import './css.css';


class HappyGraph extends Component {
   
    render(){
        console.log(this.props.update) 
        return ( <div>
        <div id="HappyGraph_flyer">
        
             <div id="HappyGraph_title">HappyGraph 
             </div>
             <div id="HappyGraph_mainlabel">Pseudonimous Social Network 
             </div>
            <div id="HappyGraph_label">Research #5 - ongoing</div>
        </div>
        <div id="HappyGraph_browser">
            <input  className="HappyGraph_footer_button" type="button" value=" Vision" onClick={() => this.props.update("GraphVision")}/>           
        </div>
        <div  id="HappyGraph_panel">
        Social Data
        </div>
        <div id="Proxy_footer">
            <input  className="HappyGraph_footer_button" type="button" value="  Res & Dev" onClick={() => this.props.update("Research")}/>       
        </div>
    </div>
        );
    }


}

function jump(web){
    window.open(web)
  }


export default HappyGraph
