import React, { Component } from 'react'
import Web3 from 'web3'

import './css.css';


let value="xxx";
class Claimer extends Component {

    constructor(props){
        super()
        this.state={value:"xxx"}
    }
        change(txt){
            console.log(txt)
            this.setState={value:txt}
            this.render()
        }

    componentDidMount(){
        this.loadData()
    }

    async approve(){
        window.ethereum.enable().then(async function (accounts) {  
            
            const beans = new web3.eth.Contract(tokenABI, BEANS) 
            let total=await beans.methods.allowance(accounts[0],PERIODICSTAKER).call()
            let isApproved=(total>0)?true:false 

            if(!isApproved){
            const beans = new web3.eth.Contract(tokenABI, BEANS) 
            alert("approving")
            await beans.methods.approve(PERIODICSTAKER,web3.utils.toBN(web3.utils.toWei("10000"))).send({gasPrice: '12345678', from: accounts[0]})
            alert("approved1")
            document.getElementById("LockDrop_stake_button").value=" plant beans!"
            }else{
                const lockdrop = new web3.eth.Contract(LOCKDROPABI, LOCKDROPBEANS) 
                if(document.getElementById("LockDrop_stake_amount").value==""){
                    alert("Plant an amount of BEANS > 0")
                }else{
                
                document.getElementById("LockDrop_stake_button").value=" planting..."
                await lockdrop.methods.LockDrop(web3.utils.toBN(web3.utils.toWei(document.getElementById("LockDrop_stake_amount").value))).send({gasPrice: '12345678', from: accounts[0]})
                this.loadData()
                document.getElementById("LockDrop_stake_button").value=" plant beans!"
            }
            }
    })   
        
    }



    async loadData(){
        window.ethereum.enable().then(async function (accounts) {   
            
            const beans = new web3.eth.Contract(tokenABI, BEANS) 
            let total=await beans.methods.balanceOf(LOCKDROPBEANS).call()
            document.getElementById("LockDrop_reward_total").innerHTML=" "+(total/1000000000000000000)+" Beans "

            const lockdrop = new web3.eth.Contract(LOCKDROPABI, LOCKDROPBEANS) 
            let multiplier=await lockdrop.methods.multiplier().call()
            document.getElementById("LockDrop_reward_description").innerHTML="Plant Beans and grow them "+multiplier+"%"

 
            let allowance=await beans.methods.allowance(accounts[0],PERIODICSTAKER).call()
            let isApproved=(allowance>0)?true:false 
            if(isApproved)document.getElementById("LockDrop_stake_button").value=" plant beans!"
            if(!isApproved)document.getElementById("LockDrop_stake_button").value=" approve beans"

    })
    }

    render(){
      
       
        return ( 
        <div className="LockDrop_module">
            <div><span className="pointer" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => {window.open("https://etherscan.io/address/0xdFCe2f79f8c3Bf1765D9563a6b1BDAfb117E72BF")}}></span> Claimer</div>
            <div className="LockDrop_panel">
            <div className="LockDrop_board">
            <div className="LockDrop_reward_token" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => {window.open("https://etherscan.io/token/0x801F90f81786dC72B4b9d51Ab613fbe99e5E4cCD")}}></div>
            <div className="LockDrop_reward_description" id="LockDrop_reward_description" aria-labelledby="jsx-a11y/accessible-emoji" role="img">Claim a badge from a stake!</div>
            <div className="LockDrop_reward_available" aria-labelledby="jsx-a11y/accessible-emoji" role="img">available:</div>
            <div className="LockDrop_reward_total pointer" id="LockDrop_reward_total" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => {window.open("https://etherscan.io/tokenholdings?a=0xdFCe2f79f8c3Bf1765D9563a6b1BDAfb117E72BF")}}> 1000 Berries </div>      
            <center>coming soon..</center>
            <input  id="LockDrop_stake_button" className="LockDrop_stake_button" type="button" value=" Claim now!"/> 
            

            </div>
            </div>
        </div>
            
        );

    

   
    }


}

    let LOCKDROPABI=[ { "inputs": [], "name": "burn", "outputs": [ { "internalType": "bool", "name": "", "type": "bool" } ], "stateMutability": "nonpayable", "type": "function" }, { "inputs": [ { "internalType": "uint256", "name": "amount", "type": "uint256" } ], "name": "LockDrop", "outputs": [], "stateMutability": "nonpayable", "type": "function" }, { "inputs": [], "stateMutability": "nonpayable", "type": "constructor" }, { "inputs": [], "name": "multiplier", "outputs": [ { "internalType": "uint256", "name": "", "type": "uint256" } ], "stateMutability": "view", "type": "function" }, { "inputs": [], "name": "receiver", "outputs": [ { "internalType": "address", "name": "", "type": "address" } ], "stateMutability": "view", "type": "function" }, { "inputs": [], "name": "staker", "outputs": [ { "internalType": "contract PeriodicStaker", "name": "", "type": "address" } ], "stateMutability": "view", "type": "function" }, { "inputs": [], "name": "token", "outputs": [ { "internalType": "contract ERC20", "name": "", "type": "address" } ], "stateMutability": "view", "type": "function" } ]
